<template>
  <a
    v-bind:href="link"
    v-on:mouseenter="onMouseEnter"
    v-on:mouseleave="onMouseLeave"
    class="menu-background" v-bind:isActive="isMain"
  >
    <span id="menu-left" v-bind:class="icon"></span>
    <span id="menu-center">{{ title }}</span>
    <span class="menu-point-active" v-bind:isActive="active"></span>
  </a>
</template>

<script lang="ts">
import { ref, onBeforeMount } from 'vue'

interface Props {
  link: string;
  icon: string;
  title: string;
  isMain: boolean;
}

export default {
  setup(props: Props) {
    const active = ref(false);

     function onMouseEnter() {
      active.value = true;
    }

    function onMouseLeave() {
      active.value = props.isMain
    }

    const beforeMount = onBeforeMount(() => {
      onMouseLeave();
    });

    const link = ref(props.link);
    const icon = ref(props.icon);
    const title = ref(props.title);
    const isMain = ref(props.isMain);

    return { link, icon, title, isMain, active, onMouseEnter, onMouseLeave, beforeMount };
  }
}
</script>

