
import { ref, computed } from 'vue'

interface Props {
  link?: String,
  text?: String,
}

export default {
  props: {
    link: {
      type: String,
      require: true,
    },
    text: {
        type: String,
        require: true,
    }
  },
  setup(props: Props) {
    const link = computed(() => {
        return props.link ?? "";
    });

    const text = computed(() => {
        return props.text ?? "";
    });

    return { link, text }
  },
}
